html {
  scroll-snap-type: y mandatory;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
  scroll-behavior:smooth;
  height: 200vh;
}

body {
  box-sizing: border-box;
  margin: 0;
  height: 100%;
  scroll-snap-type: y mandatory;
  -webkit-overflow-scrolling: touch;
  scroll-behavior:smooth;
  overflow-y: scroll;
}

.min-h-item {
  min-height: 150px;
}

@media screen and (max-width: 600px) {
  .min-h-item {
    min-height: 100px;
  }

  html {
    scroll-snap-type: none;
    -webkit-overflow-scrolling: auto;
  }

  body {
    scroll-snap-type: none;
    -webkit-overflow-scrolling: auto;
  }
}


rect {
  opacity: .85;
}